import { Component, ViewChild, ElementRef } from '@angular/core';
import { NavController } from 'ionic-angular';
import { Geolocation } from '@ionic-native/geolocation';
import { ToastController } from 'ionic-angular';

declare var google:any


@Component({ 
  selector: 'page-home',
  templateUrl: 'home.html'
})

export class HomePage {
latitude;
longitude;
marker;

@ViewChild('map') mapRef: ElementRef;
map:any
  constructor(public navCtrl: NavController, private geolocation: Geolocation,private toastCtrl: ToastController) {

  }

  //nav loaded
  ionViewDidLoad(){
    console.log(this.mapRef);
    this.showMap()
  } 

  //get location and display toast and marker
  getLocation(){
    let toast = this.toastCtrl.create({
      message: 'Triangulating....',
      duration: 3000,
      position: 'top'
    });
  
    toast.present();

this.geolocation.getCurrentPosition().then((resp) => {
 console.log(resp.coords.latitude + ',' + resp.coords.longitude);
  this.latitude=resp.coords.latitude;
  this.longitude= resp.coords.longitude


  let toast = this.toastCtrl.create({
    message: 'Marker placed .... Fly to location '  + '\n',
    duration: 5000,
    position: 'top'
  });

  toast.present();

  var position = new google.maps.LatLng(this.latitude,this.longitude)
  this.marker = new google.maps.Marker({
   position,
   title:"You're here!"})
   this.marker.setMap(this.map);

 }).catch((error) => {
   console.log('Error getting location', error);
 });
 
 
  }

  //delete marker
delMarker(){
  if(this.marker && this.marker !=0){
  this.marker.setMap(null);
  this.marker = 0;}
  
}

//fly to location with marker
flyOver(){
  if(this.latitude !=null && this.longitude !=null){
  this.map.panTo({lat:this.latitude, lng: this.longitude});
  this.map.setZoom(15);}
  
}

//show map
  showMap(){

    const location = new google.maps.LatLng(5.7645823,-0.2192815);

    //My map options
    const options = {
      center:location,
      zoom:12,
      
      styles:[ {
        "stylers": [
            {
                "hue": "#dd0d0d"
            }
        ]
    },
    {
        "featureType": "road",
        "elementType": "labels",
        "stylers": [
            {
                "visibility": "off"
            }
        ]
    },
    {
        "featureType": "road",
        "elementType": "geometry",
        "stylers": [
            {
                "lightness": 100
            },
            {
                "visibility": "simplified"
            }
        ]
    }]
    }

    this.map = new google.maps.Map(this.mapRef.nativeElement,options)
  }

  addMarker(position, map){
 
  }

}



